.right-section {
	background-color: rgba(46, 38, 38, 1);
	position: absolute;
	width: 50px;
	height: 150px;
	right: 0px;
	top: 40%;
	padding: 5px;
	border-radius: 10px 0 0 10px;
	justify-content: center;
	display: flex;
}

.images {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.images img {
	height: 20px;
	margin: 10px;
	width: 30px;
}
